
*{
    padding: 0;
    margin: 0;
    font-family: "Jost", sans-serif;
}

body{
    background:aliceblue;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    height: 100vh;
    width: 100vw;
    font-family: "jost",sans-serif;
}

html{
    font-size: 100%;
    height: 100%;
}

html{
    background:cornsilk;
}
.box{
    width: 25vw;
    height: 60vh;
    border-radius: 0.5rem;
    box-shadow: 0 0.2rem 3rem black;
    background: cadetblue;
    position: relative;
    overflow: hidden;
    transform: translate3d(0,0,0);
    min-width: 20rem;
    min-height: 35rem;
}

.wave{
    opacity: 0.3;
    position: absolute;
    top: 120%;
    left: 50%;
    background: white;
    width: 50rem;
    height: 50rem;
    margin-left: -25rem;
    margin-top: -20rem;
    transform-origin: 50% 48%;
    border-radius: 43%;
    animation: drift 3000ms infinite linear;
    z-index: 1;
}

.wave-three{
    animation: drift 5000ms infinite linear;
    z-index: 2 !important;
    opacity: 0.2;
}

.wave-two{
    animation: drift 7000ms infinite linear;
    z-index: 3 !important;
    opacity: 0.1;
}

.box::after{
    content: "";
    display: block;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 11;
    transform: translate3d(0,0,0);
}

.inputField{
    width: 75%;
    /* border: 3px solid #00b4cc; */
    border: none;
    margin: 30px;
    margin-left: 50px;
    padding: 20px;
    height: 20px;
    border-radius: 15px;
    outline: none;
    font-size: 18px;
}
.errormsg{
    text-align: center;
    color: white;
    font-size: 30px;
}

.location{
    font-size: 50px;
    text-align: center;
    padding: 20px;
}

.temp{
    text-align: center;
    font-size: 35px;
}

.tempmin_max{
    text-align: center;
    padding-top: 60px;
}